import React from "react";
class About extends React.Component {
  constructor() {
    super();
    this.state = {
      skills: [
        { id: "HTML5_skill", content: "C Programming", porcentage: "90%", value: "90" },
        { id: "CSS3_skill", content: "C++", porcentage: "70%", value: "70" },
        {
          id: "JavaScript_skill",
          content: "JavaScript",
          porcentage: "60%",
          value: "60"
        },
        //{ id: "PHP_skill", content: "PHP", porcentage: "70%", value: "70" },
        /*{
          id: "ReactJS_skill",
          content: "ReactJS",
          porcentage: "80%",
          value: "80"
        }, */
        {
          id: "Python_skill",
          content: "Mysql",
          porcentage: "90%",
          value: "90"
        },
        {
          id: "VanillaJS_skill",
          content: "Node",
          porcentage: "50%",
          value: "50"
        },
        {
          id: "Wordpress_skill",
          content: "HTML5/CSS",
          porcentage: "80%",
          value: "80"
        }
        
      ],
      about_me: [
        {
          id: "first-p-about",
          content:
            "Hi, My name is Ashish Kumar. I am pursuing B.tech at IES Institute Of technology and management, Bhopal stream of electronics and communication. My interest in Software development using C++ & Python and web development using JavaScript and React with the help of the Node."
        },
        {
          id: "second-p-about",
          content:
            "Am done some achievement in my name like Elite Rank in IoT from IIT Kharagpur. I am on the way to train my self for problem-solving and develop a new kind of futuristic website. In my college & in my academic project, coordinating is the best part of me. I get certificated from hacker rank for problem-solving and more of the certification program is going on, in various type of platform."
        },
        {
          id: "third-p-about",
          content:
            " "
        }
      ]
    };
  }

  render() {
    return (
      <section id="about" className="about-mf sect-pt4 route">
        <div className="container">
          <div className="row">
            <div className="col-sm-12">
              <div className="box-shadow-full">
                <div className="row">
                  <div className="col-md-6">
                    <div className="row">
                      <div
                        className="col-sm-6 col-md-5"
                        style={{ margin: "0 auto" }}
                      >
                        <div
                          className="about-img"
                          style={{ textAlign: "center" }}
                        >
                          <img
                            className="img-fluid rounded b-shadow-a"
                            alt=""
                          />
                        </div>
                      </div>
                    </div>
                    <div className="skill-mf">
                      {/* <p className="title-s">Skill</p> */}
                      {this.state.skills.map(skill => {
                        return (
                          <React.Fragment key={skill.id}>
                            <span>{skill.content}</span>{" "}
                            <span className="pull-right">
                              {skill.porcentage}
                            </span>
                            <div className="progress">
                              <div
                                className="progress-bar"
                                role="progressbar"
                                style={{ width: skill.porcentage }}
                                aria-valuenow={skill.value}
                                aria-valuemin="0"
                                aria-valuemax="100"
                              ></div>
                            </div>
                          </React.Fragment>
                        );
                      })}
                    </div>
                  </div>
                  <div className="col-md-6">
                    <div className="about-me pt-4 pt-md-0">
                      <div className="title-box-2">
                        <h5 className="title-left">About Me</h5>
                      </div>
                      {this.state.about_me.map(content => {
                        return (
                          <p className="lead" key={content.id}>
                            {content.content}
                          </p>
                        );
                      })}
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </section>
    );
  }
}

export default About;
